.cityweather{
 background:#FAFAFA;
 width: 200px;
 height: 300px;
 margin: 15px 15px;
 padding: 30px;
 color:#000000;
 display: flex;
 flex-direction: column;
 align-items: center;
 justify-content: space-between;
 border-radius: 10px;
 text-transform:capitalize;
 position: relative;

}
.cityweater__close{
 color: gray;
 position: absolute;
  top: 16px;
  right: 16px;
}

@media only screen and (max-width: 600px) {
  .cityweather{
    height: 240px;
    padding-top: 0;
  }
  .cityweather h1{
    font-size: 1.5rem;
    margin: 0 0 5 0;
  }
}
